import cppImage from './img/c-.png';  // Import de l'image
import csharpImage from './img/c-sharp.png';  // Import de l'image
import unityImage from './img/unity.png';  // Import de l'image
import javaImage from './img/js.png';  // Import de l'image
import gestionImage from './img/gestionR.png';  // Import de l'image
import teamImage from './img/gestion.jpg';  // Import de l'image
const Compet = () => {
    return (
    <div>
      <div className='m-8 mb-12'>
      <h1 className="text-4xl font-bold font-sans ">Mes comptences</h1>
      </div>
      
      <div className=' flex justify-center space-x-16 m-4'>
      
        <div className=' group my-custom-hover'>
          <img className="w-16 h-16 mx-auto "src= {csharpImage} alt="logo C#" />
          <h2 className="title-compet ">Dveloppeur C#</h2>
          <p className="para-compet">
          Je possde des comptences en C#, que j'ai mises en pratique en dveloppant plusieurs jeux vido.</p>
        </div>
        <div className='group my-custom-hover'>
          <img className="w-16 h-16 mx-auto "src= {cppImage} alt="logo cpp" />
          <h2 className="title-compet">Dveloppeur C++</h2>
          <p className="para-compet">
          Je matrise le C++, un langage qui m'a permis de raliser diffrents projets.
          Ces expriences m'ont appris l'optimisation et la rigueur.</p>
        </div>
        
      </div>
      <div className=' flex justify-center space-x-16 m-4'>
        <div className=' group my-custom-hover'>
          <img className="w-16 h-16 mx-auto "src= {unityImage} alt="logo C#" />
          <h2 className="title-compet">Unity</h2>
          <p className="para-compet">
          Je matrise Unity, un moteur que j'ai utilis pour concevoir plusieurs jeux vido. 
          Cela m'a permis de donner vie  des projets plus ambitieux.</p>
        </div>
        <div className='group my-custom-hover'>
          <img className="w-16 h-16 mx-auto "src= {javaImage} alt="logo cpp" />
          <h2 className="title-compet">Javascript</h2>
          <p className="para-compet">
          Je possde des comptences en JavaScript, que j'ai utilises avec React et Node.js 
          pour dvelopper des applications web dynamiques et performantes.</p>
        </div>
      </div>
      <div className=' flex justify-center space-x-16 m-4'>
        <div className=' group my-custom-hover'>
          <img className="w-16 h-16 mx-auto "src= {teamImage} alt="logo C#" />
          <h2 className="title-compet">Travail en quipe</h2>
          <p className="para-compet">
            Avec les nombreux projets de groupe sur lesquels j'ai travaill, j'ai dvelopp de solides comptences en collaboration et en communication. 
          </p>
        </div>
        <div className='group my-custom-hover'>
          <img className="w-16 h-16 mx-auto "src= {gestionImage} alt="logo cpp" />
          <h2 className="title-compet">Gestion de projet</h2>
          <p className="para-compet">
          En travaillant sur de nombreux projets d'quipe, 
          j'ai dvelopp une relle aptitude  grer efficacement les diffrentes tapes de ralisation.</p>
        </div>
      </div>
    </div>
    );
};
export default Compet;